.redemption-form {
  max-width: 700px;
}

.redemption-form h3 {
  color: #61dafb;
  margin-bottom: 1rem;
}

.rewards-info {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.info-card {
  background-color: #1a1a1a;
  padding: 1rem;
  border-radius: 4px;
  margin-bottom: 1.5rem;
}

.info-card h4 {
  color: #61dafb;
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

.progress-display {
  font-size: 1.8rem;
  font-weight: bold;
  color: #ffffff;
}

.available-rewards h4 {
  color: #61dafb;
  margin-bottom: 1rem;
}

.reward-option {
  background-color: #1a1a1a;
  border: 2px solid #333;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  transition: border-color 0.2s, background-color 0.2s;
}

.reward-option:hover {
  border-color: #444;
  background-color: #222;
}

.reward-option input[type="radio"] {
  margin-top: 0.25rem;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.reward-option input[type="radio"]:disabled {
  cursor: not-allowed;
}

.reward-option label {
  flex: 1;
  cursor: pointer;
  margin-bottom: 0;
}

.reward-option label.disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.reward-name {
  font-size: 1.1rem;
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 0.5rem;
}

.reward-details {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  font-size: 0.9rem;
}

.available-badge {
  background-color: #4caf50;
  color: #fff;
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-weight: 600;
  font-size: 0.85rem;
  display: inline-block;
  width: fit-content;
}

.cost {
  color: #888;
}

.not-available {
  color: #ff4444;
}

.redemption-form button[type="submit"] {
  width: 100%;
  margin-top: 1rem;
}

.redemption-form button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
