.admin-queue {
  max-width: 1200px;
  margin: 0 auto;
}

.queue-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.queue-header h3 {
  color: #61dafb;
  margin: 0;
}

.no-submissions {
  text-align: center;
  padding: 3rem;
  color: #888;
}

.submissions-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.submission-item {
  background-color: #1a1a1a;
  border-radius: 8px;
  padding: 1.5rem;
  display: grid;
  grid-template-columns: 1fr auto auto;
  gap: 1.5rem;
  align-items: center;
}

.submission-info {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.info-row {
  display: flex;
  gap: 0.5rem;
}

.info-row strong {
  color: #61dafb;
  min-width: 90px;
}

.submission-screenshot {
  display: flex;
  align-items: center;
}

.screenshot-link {
  padding: 0.75rem 1.5rem;
  background-color: #282c34;
  color: #61dafb;
  border-radius: 4px;
  text-decoration: none;
  transition: background-color 0.2s;
}

.screenshot-link:hover {
  background-color: #333;
  text-decoration: none;
}

.submission-actions {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  min-width: 120px;
}

.submission-actions button {
  width: 100%;
}

@media (max-width: 768px) {
  .submission-item {
    grid-template-columns: 1fr;
  }

  .submission-screenshot {
    justify-content: center;
  }

  .submission-actions {
    flex-direction: row;
  }
}
