.bulk-submission-form {
  max-width: 100%;
}

.bulk-submission-form h3 {
  color: #61dafb;
  margin-bottom: 1rem;
}

.form-description {
  color: #ccc;
  margin-bottom: 2rem;
  line-height: 1.6;
}

.dungeons-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.dungeon-submission-card {
  background-color: #1a1a1a;
  border-radius: 8px;
  padding: 1.5rem;
  border: 2px solid #333;
  transition: border-color 0.2s;
}

.dungeon-submission-card:focus-within {
  border-color: #61dafb;
}

.dungeon-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid #333;
}

.dungeon-header h4 {
  color: #61dafb;
  margin: 0;
  font-size: 1.1rem;
}

.btn-clear {
  padding: 0.25rem 0.75rem;
  background-color: #ff4444;
  color: #fff;
  border: none;
  border-radius: 4px;
  font-size: 0.85rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.btn-clear:hover:not(:disabled) {
  background-color: #cc0000;
}

.btn-clear:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.dungeon-fields {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.field-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.field-group label {
  color: #ccc;
  font-size: 0.9rem;
  font-weight: 500;
}

.field-group input[type="number"],
.field-group input[type="file"] {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #444;
  border-radius: 4px;
  background-color: #282c34;
  color: #ffffff;
  font-size: 0.95rem;
}

.field-group input[type="file"] {
  padding: 0.5rem;
  cursor: pointer;
}

.field-group input:focus {
  outline: none;
  border-color: #61dafb;
}

.field-group input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.file-selected-small {
  font-size: 0.85rem;
  color: #4caf50;
  padding: 0.5rem;
  background-color: #282c34;
  border-radius: 4px;
  word-break: break-all;
}

.error-small {
  color: #ff4444;
  font-size: 0.85rem;
  margin-top: 0.5rem;
  padding: 0.5rem;
  background-color: rgba(255, 68, 68, 0.1);
  border-radius: 4px;
}

.form-actions {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}

.btn-large {
  padding: 1rem 3rem;
  font-size: 1.1rem;
  font-weight: 600;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .dungeons-grid {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 1400px) {
  .dungeons-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
