.user-dashboard {
  max-width: 1400px;
  margin: 0 auto;
}

.dashboard-header {
  margin-bottom: 2rem;
}

.tab-buttons {
  display: flex;
  gap: 0.5rem;
  margin-top: 1rem;
  flex-wrap: wrap;
}

.tab-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  background-color: #1a1a1a;
  color: #ffffff;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
}

.tab-btn:hover {
  background-color: #333;
}

.tab-btn.active {
  background-color: #61dafb;
  color: #000;
  font-weight: 600;
}

.progress-tab h3 {
  margin-bottom: 1.5rem;
  color: #61dafb;
}

.dungeons-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
}

.submit-tab, .redeem-tab, .history-tab {
  background-color: #282c34;
  border-radius: 8px;
  padding: 2rem;
}

.status-badge {
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
}

.status-pending {
  background-color: #ffa500;
  color: #000;
}

.status-verified {
  background-color: #4caf50;
  color: #fff;
}

.status-rejected {
  background-color: #ff4444;
  color: #fff;
}
