.home {
  max-width: 1200px;
  margin: 0 auto;
}

.welcome-section {
  text-align: center;
  padding: 2rem;
}

.season-info {
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #444;
}

.season-info h3 {
  color: #61dafb;
  margin-bottom: 0.5rem;
}

.dashboard-stats {
  margin-top: 2rem;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1.5rem;
  margin-top: 1rem;
}

.stat-item {
  text-align: center;
  padding: 1rem;
  background-color: #1a1a1a;
  border-radius: 8px;
}

.stat-value {
  font-size: 2rem;
  font-weight: bold;
  color: #61dafb;
}

.stat-label {
  margin-top: 0.5rem;
  color: #888;
  font-size: 0.9rem;
}

.top-dungeon {
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid #444;
  text-align: center;
}

.leaderboard-section {
  margin-top: 2rem;
}

.current-user {
  background-color: #1a3a4a !important;
  font-weight: bold;
}

.info-section {
  margin-top: 2rem;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-top: 1rem;
}

.info-item {
  padding: 1.5rem;
  background-color: #1a1a1a;
  border-radius: 8px;
}

.info-item h3 {
  color: #61dafb;
  margin-bottom: 0.75rem;
}

.info-item p {
  color: #ccc;
  line-height: 1.6;
}

/* Quick Start Section */
.quick-start-section {
  margin-top: 2rem;
  background: linear-gradient(135deg, #1a3a4a 0%, #2d5a6e 100%);
  border: 2px solid #61dafb;
  box-shadow: 0 4px 16px rgba(97, 218, 251, 0.2);
}

.quick-start-highlight {
  animation: pulse-glow 2s ease-in-out infinite;
}

@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 4px 16px rgba(97, 218, 251, 0.2);
  }
  50% {
    box-shadow: 0 4px 24px rgba(97, 218, 251, 0.4);
  }
}

.quick-start-content {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid rgba(97, 218, 251, 0.2);
}

.quick-start-icon {
  font-size: 4rem;
  animation: bounce 2s ease-in-out infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.quick-start-text {
  flex: 1;
}

.quick-start-text h2 {
  color: #61dafb;
  margin-bottom: 1rem;
  font-size: 2rem;
}

.quick-start-text p {
  color: #ccc;
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  line-height: 1.6;
}

.quick-start-steps {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}

.step {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  border-left: 3px solid #61dafb;
}

.step-number {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  background-color: #61dafb;
  color: #000;
  font-weight: bold;
  border-radius: 50%;
  flex-shrink: 0;
}

.step span:last-child {
  color: #fff;
  line-height: 1.4;
}

.btn-large {
  padding: 1rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.btn-large:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(97, 218, 251, 0.3);
}

/* Responsive */
@media (max-width: 768px) {
  .quick-start-content {
    flex-direction: column;
    text-align: center;
  }

  .quick-start-steps {
    grid-template-columns: 1fr;
  }

  .quick-start-text h2 {
    font-size: 1.5rem;
  }
}
