.super-admin-panel {
  max-width: 1400px;
  margin: 0 auto;
}

.super-admin-header {
  text-align: center;
  margin-bottom: 2rem;
}

.super-admin-header h2 {
  color: #61dafb;
  margin-bottom: 0.5rem;
}

.super-admin-header p {
  color: #888;
}

.system-stats {
  margin-bottom: 2rem;
}

.system-stats h3 {
  color: #61dafb;
  margin-bottom: 1rem;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.stat-box {
  background-color: #1a1a1a;
  padding: 1.5rem;
  border-radius: 8px;
  text-align: center;
}

.stat-label {
  color: #888;
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.stat-value {
  font-size: 2rem;
  font-weight: bold;
  color: #61dafb;
}

.seasons-section,
.users-section {
  margin-bottom: 2rem;
}

.seasons-section h3,
.users-section h3 {
  color: #61dafb;
  margin-bottom: 1rem;
}

.admin-badge {
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
}

.level-none {
  background-color: #444;
  color: #ccc;
}

.level-admin {
  background-color: #ffa500;
  color: #000;
}

.level-super_admin {
  background-color: #ff4444;
  color: #fff;
}

.action-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.btn-small {
  padding: 0.4rem 0.8rem;
  font-size: 0.85rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-small.btn-primary {
  background-color: #61dafb;
  color: #000;
}

.btn-small.btn-primary:hover {
  background-color: #4fa8c5;
}

.btn-small.btn-success {
  background-color: #4caf50;
  color: #fff;
}

.btn-small.btn-success:hover {
  background-color: #45a049;
}

.btn-small.btn-danger {
  background-color: #ff4444;
  color: #fff;
}

.btn-small.btn-danger:hover {
  background-color: #cc0000;
}

.status-active {
  background-color: #4caf50;
  color: #fff;
}

.status-completed {
  background-color: #888;
  color: #fff;
}
